import logo from "../images/logo.svg";
import { footer } from "./data";

function Footer() {
  return (
    <footer className="Footer">
      <div className="content container">
        <img className="footer-logo" src={logo} alt={logo}></img>
        <div className="footer-menu">
          {footer.menu.map((menuItem) => (
            <MenuGroup key={menuItem.id} {...menuItem} />
          ))}
        </div>
        <div className="socials">
          {footer.socials.map(({ id, url, image: Svg }) => (
            <a key={id} href={url}>
              <Svg />
            </a>
          ))}
        </div>
      </div>
    </footer>
  );
}

const MenuGroup = ({ groupTitle, links }) => {
  return (
    <div className="menuGroup">
      <h4>{groupTitle}</h4>
      {links.map((link) => (
        <a key={link.text} href={link.url}>
          {link.text}
        </a>
      ))}
    </div>
  );
};

export { Footer };
