import { Menu } from "../Header/Menu";
import logo from "../images/logo.svg";
import { IoMenu } from "react-icons/io5";
import { motion, AnimatePresence } from "framer-motion";
import React, { useState, useEffect } from "react";
import { Modal } from "../Modal/Modal";
import { SignUp } from "../Form/SignUp";
import { Login } from "../Form/Login";

function Header() {
  const [isMobile, setIsMobile] = useState(window.innerWidth < 767);
  const [showMobileMenu, setShowMobileMenu] = useState(false);
  const [isSignupOpen, setSignupOpen] = useState(false);
  const [isLoginOpen, setLoginOpen] = useState(false);

  const openSignup = () => {
    setSignupOpen(true);
  };
  const closeSignup = () => setSignupOpen(false);
  const openLogin = () => setLoginOpen(true);
  const closeLogin = () => setLoginOpen(false);

  const menuItems = [
    {
      url: "#",
      text: "Features",
    },
    {
      url: "#",
      text: "Prices",
    },
    {
      url: "#",
      text: "Resources",
    },
  ];

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth < 767);
    };

    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  return (
    <>
      <header className="header container">
        <img src={logo} alt="logo" className="logo" />
        {isMobile ? (
          <IoMenu
            className="burger"
            onClick={() => setShowMobileMenu(!showMobileMenu)}
          />
        ) : (
          <Menu links={menuItems} />
        )}
        <AnimatePresence>
          {showMobileMenu && isMobile && (
            <motion.div
              initial={{ opacity: 0, height: 0 }}
              animate={{ opacity: 1, height: "auto" }}
              exit={{ opacity: 0, height: 0 }}
              className="mobileMenu"
            >
              <Menu
                links={menuItems}
                handleSignup={openSignup}
                handleLogin={openLogin}
              />
            </motion.div>
          )}
        </AnimatePresence>
      </header>
      <Modal open={isSignupOpen} title="Sign Up" handleClose={closeSignup}>
        <SignUp closeModal={closeSignup} />
      </Modal>
      <Modal open={isLoginOpen} title="Sign In" handleClose={closeLogin}>
        <Login closeModal={closeLogin} />
      </Modal>
    </>
  );
}

export { Header };
