import { useSelector } from "react-redux";
import { motion, AnimatePresence } from "framer-motion";
import { selectLinks } from "../../store/slice/linkSlice";
import { Button } from "../Button/Button";
import { useState } from "react";

const Shortens = () => {
  const [copiedLinks, setCopiedLink] = useState(null);
  const links = useSelector(selectLinks);
  const btnSC = {
    text: "copy",
    btnCN: "button medium square",
  };
  const btnCopied = {
    text: "Copied!",
    btnCN: "button medium square",
  };
  const copyToClipboard = (link) => {
    navigator.clipboard.writeText(link).then(() => {
      setCopiedLink(link);
    });
  };

  if (!links?.length) return null;

  return (
    <section className="Shortens">
      <div className="container">
        {links.map((item) => (
          <AnimatePresence key={item.code}>
            <motion.div
              className="sh-item"
              initial={{ opacity: 0, height: 0 }}
              animate={{ opacity: 1, height: "auto" }}
              data-active={copiedLinks === item.full_short_link2}
            >
              <span>{item.original_link}</span>
              <span>{item.full_short_link2}</span>
              <Button
                handle={() => copyToClipboard(item.full_short_link2)}
                {...(copiedLinks === item.full_short_link2 ? btnCopied : btnSC)}
              />
            </motion.div>
          </AnimatePresence>
        ))}
      </div>
    </section>
  );
};

export { Shortens };
