import { Button } from "../Button/Button";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import {
  createShortLink,
  selectLinks,
  selectLoading,
} from "../../store/slice/linkSlice";

const Form = () => {
  const loading = useSelector(selectLoading);
  const dispatch = useDispatch();
  const {
    register,
    formState: { errors },
    handleSubmit,
    reset,
  } = useForm({ mode: "onSubmit" });
  const onSubmit = (url) => {
    dispatch(createShortLink(url.Url));
    reset();
  };

  const btnSI = {
    text: "Shorten it!",
    btnCN: "button medium square",
  };
  return (
    <section className="section">
      <div className="container">
        <form
          className="form"
          onSubmit={handleSubmit(onSubmit)}
          autoComplete="off"
        >
          <input
            type="url"
            placeholder="Shorten a link here..."
            className="input"
            style={{
              outlineColor: errors.Url ? "red" : "currentcolor",
            }}
            {...register("Url", {
              required: "Please add a link",
              pattern: {
                value:
                  /(http(s)?:\/\/.)?(www\.)?[-a-zA-Z0-9@:%._+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_+.~#?&//=]*)/g,
                message: "Please enter a valid url",
              },
            })}
            disabled={loading === "loading"}
          />
          <Button {...btnSI} disabled={loading === "loading"} />
          {errors.Url && <div className="error"> {errors.Url.message}</div>}
        </form>
      </div>
    </section>
  );
};

export { Form };
