import { Button } from "../Button/Button";

function CallToAction() {
  const btnGS = {
    btnCN: "button medium",
    text: "Get Started",
  };
  return (
    <section className="CallToAction">
      <h2>Boost your links today</h2>
      <Button {...btnGS} />
    </section>
  );
}

export { CallToAction };
