import { Button } from "../Button/Button";

function Menu({ links = [], handleSignup, handleLogin }) {
  const token = null;
  const btnLog = {
    btnCN: "button medium link",
    text: "Login",
  };
  const btnSU = {
    text: "Sign Up",
    btnCN: "button medium",
  };
  const btnLOUT = {
    text: "Log out",
    btnCN: " button medium",
  };

  return (
    <div className="header-menu">
      <div className="pages">
        {links.map((link, index) => (
          <a href={link.url} key={index} className="link">
            {link.text}
          </a>
        ))}
      </div>
      <div className="login">
        {token ? (
          <Button {...btnLOUT} />
        ) : (
          <>
            <Button handle={handleLogin} {...btnLog} />
            <Button handle={handleSignup} {...btnSU} />
          </>
        )}
      </div>
    </div>
  );
}

export { Menu };
